<template>
    <v-autocomplete
        v-model="binding.value"
        :label="binding.fieldTitle ? binding.fieldTitle : binding.title"
        :items="items"
        :loading="loading"
        :search-input.sync="search"
        no-data-text="Type at least 3 characters to search..."
        cache-items
        clearable
        :multiple="binding.isMultiple == true"
    ></v-autocomplete>
</template>
<script>
  import axios from "axios";

  export default {
    data: () => ({
        search: null,
        loading: false,
        items: []
    }),
    props: {
      binding: {
        type: Object
      }
    },
    watch: {
      search (val) {
        if (this.timeout) 
          clearTimeout(this.timeout); 

        this.timeout = setTimeout(() => {
          val && val != this.binding.value && val.length > 2 && this.querySelections(val)
        }, 300); // delay
      },
    },
    methods: {
      querySelections (v) {
        this.loading = true;
        var queryString = `${this.$snowstormBase}/${this.$snowstormBranch}/concepts?activeFilter=true&term=${v}&
                            termActive=true&language=en&offset=0&limit=50&ecl=${encodeURIComponent(this.binding.ecl)}`
        axios
          .get(queryString)
          .then(response => {
            this.items = response.data.items.map( e => e.fsn.term );
            this.loading = false;
          })
      }
    }
  }
</script>
